<template>
  <div class="app">
    <header>
      <div class="logo">
        <img src="./img/rtflogo.png" alt="">
        <div class="logotext">Система мониторинга учебной нагрузки преподавателей ИРИТ-РТФ</div>
      </div>
      <button><img class="menu" src="./img/menu.png" alt=""></button>
    </header>
    <span class="reg mt-110">{{ name }}</span>
    <button class="reg" @click="login" v-if="showSingIn">Войти</button>
    <button class="reg" @click="logout" v-if="!showSingIn">Выйти</button>
    <div class="table" v-if="!showSingIn"><AGGrid :gridData="gridData"></AGGrid></div>
<!--    <form class="reg" id="search">-->
<!--    Поиск:ㅤ-->
<!--    <input  name="query" v-model="searchQuery">-->
<!--    </form>-->
<!--    <Grid :data="gridData" :columns="gridColumns" :filter-key="searchQuery" ></Grid>-->
  </div>
</template>

<script>
import Header from "@/components/Header.vue";
import Grid from "@/components/Grid.vue";
import Keycloak from "keycloak-js";
import AGGrid from "@/components/AGGrid.vue";

const keycloak = new Keycloak({
  url: "https://keys.urfu.ru/auth/",
  realm: "urfu-lk",
  clientId: "rtf-eduload",
  public_key:
    "",
  "token-service":
    "https://keys.urfu.ru/auth/realms/urfu-lk/protocol/openid-connect",
  "account-service": "https://keys.urfu.ru/auth/realms/urfu-lk/account",
  "tokens-not-before": 0,
});

export default {
  mounted() {
    this.fetchJson();
    this.updateKC();
  },
  components: {
    Header,
    Grid,
    AGGrid,
  },
  data() {
    return {
      showSingIn: true,
      name: "Not Auth",
      isAuth: false,
      searchQuery: '',
      gridData: [],
    };
  },
  methods: {
    async updateKC()
    {
      await keycloak.init({
      onLoad: "check-sso",
    });
    if (keycloak.authenticated) {
      this.name = keycloak.tokenParsed.user.person.title;
      this.showSingIn = false;
      let url = 'http://212.193.88.196:8080/adduser?login=' + keycloak.tokenParsed.user.person.email + '&isKeycloak=1&samAccountName=' + keycloak.tokenParsed.user.person.id;
      console.log(url);
      fetch(url).then((data) => {
    console.log(data); 
  })}
  },
    async login() {
      if (!keycloak.authenticated) {
        await keycloak.login();
      } else {
        this.name = keycloak.tokenParsed.user.person.title;
        this.isAuth = true;
      }
    },
    async logout() {
      if (keycloak.authenticated) {
        await keycloak.logout();
      }
    },
    async fetchJson() {
      let data = [];
      let json;
      await fetch("http://212.193.88.196:8080/loaddata")
        .then((resp) => resp.text())
        .then((razm) => (json = razm));
      
      data = JSON.parse(json);
      console.log(data);

      data.forEach((row) => {
        let element = {
          name: row.name,
          infoWorkPlaces: row.infoWorkPlaces,
          stake: row.stake,
          hoursOnStake: row.hoursOnStake,
          hours: row.hours,
          bHoursPlaned: row.bHoursPlaned,
          bHoursReal: row.bHoursReal,
          bHoursDiff: row.bHoursDiff,
          cHoursPlaned: row.cHoursPlaned,
          cHoursReal: row.cHoursReal,
          cHoursDiff: row.cHoursDiff,
          hoursPlaned: row.hoursPlaned,
          hoursReal: row.hoursReal,
          hoursDiff: row.hoursDiff,
          year: row.year,
        };
        this.gridData.push(element);
      });
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.reg {
  display: flex;
  margin-top: 15px;
  margin-left: 15px;
  font-size: 20px;
}
.table {
  margin: 20px;
  display: flex;
  height: 900px;
  width: 2360px;
}

header img {
  height: 100px;
}

header {
  width: 100%;
  height: 100px;
  background-color: rgb(221, 221, 221);
  flex: 1 100px;
  display: flex;
  flex-flow: row;
  align-items: center;
  justify-content: space-between;
  padding: 0px 50px;
  position: fixed;
  top: 0;
  bottom: 0;
}

header button {
  display: inline-block;
  height: 50px;
  width: 50px;
}

header .logotext {
  margin-left: 10px;
  font-size: 20px;
  font-weight: bold;
}

header .logo {
  display: flex;
  align-items: center;
}

header .menu {
  height: 49px;
  width: 49px;
}

.mt-110 {
  margin-top: 110px;
}
</style>
